export function getSituationText(score: number): string {
  if (score >= 1500) {
    return "";
  } else if (score >= 600) {
    return "";
  } else if (score >= 400) {
    return "";
  } else if (score >= 200) {
    return "";
  } else if (score >= -200) {
    return "";
  } else if (score >= -400) {
    return "";
  } else if (score >= -600) {
    return "";
  } else if (score >= -1500) {
    return "";
  } else {
    return "";
  }
}

export function scoreToPercentage(
  score: number,
  coefficientInSigmoid: number
): number {
  return 100 / (1 + Math.exp(-score / coefficientInSigmoid));
}
